import {CommService} from '../comm/comm.service'

export class Device{
  uuid: string;
  platform:string;
  online:boolean;
  alias:string;
  version:string;
  manufacturer:string;
  model:string;
  private constructor(json:any){
    for (let key in json){
      this[key]=json[key];
    }
  }
  static getDeviceList(comm:CommService):Promise<Device[]>{
    return comm.request("/device",{method:'GET'}).then((v)=>{
      const rtn:Device[]=[];
      const list=v.json();
      list.forEach((item)=>{
        rtn.push(new Device(item))
      })
      return rtn;
    }); 
  }
  static fromDeviceUuid(uuid:String, comm:CommService):Promise<Device>{
    return comm.request(`/device/${uuid}`,{method:'GET'}).then((v)=>{
      return new Device(v.json());
    })
  }
}


