import {EventEmitter, Output, Component, OnInit } from '@angular/core';
import { Device } from './Device';
import { CommService } from '../comm/comm.service';
@Component({
  selector: 'app-devicelist',
  templateUrl: './devicelist.component.html',
  styleUrls: ['./devicelist.component.css']
})
export class DevicelistComponent implements OnInit {

  @Output()
  onChoseDevice:EventEmitter<Device>;

  private deviceList: Device[] = [];
  private filterStr: string;
  private refreshing: boolean = false;
  constructor(private comm: CommService) { 
    this.onChoseDevice=new EventEmitter()
  }

  ngOnInit() {
    this.refresh();

  }

  refresh() {
    this.refreshing=true;
    Device.getDeviceList(this.comm).then((list) => {
      this.deviceList = list.sort((d1, d2) => {
        if (d1.online) {
          return -1;
        } else if (d2.online) {
          return 1;
        } else {
          return 0;
        }
      });
    }, this.comm.errorHandler).then(()=>{this.refreshing=false});
  }
  getDevices(): Device[] {
    if (this.filterStr) {
      const f = this.filterStr.toLowerCase();
      return this.deviceList.filter((item) => {
        for (var key in item) {
          if (item[key].toString && item[key].toString().toLowerCase().indexOf(f) > -1) {
            return true;
          }
        }
        return false;
      })
    } else {
      return this.deviceList;
    }
  }

  clickDevice(d:Device){
    this.onChoseDevice.emit(d);
  }
}



