import {ElementRef, Component, OnInit,ViewChild } from '@angular/core';
import { Device } from '../device/Device';
import { ActivatedRoute } from '@angular/router';
import { CommService } from '../comm/comm.service';
declare var $:any;
@Component({
  selector: 'app-client-log-detail',
  templateUrl: './client-log-detail.component.html',
  styleUrls: ['./client-log-detail.component.css']
})
export class ClientLogDetailComponent implements OnInit {
  private deviceId: string;
  device: Device;
  _status: any;
  liveLogs = [];
  @ViewChild("logModal")
  logModal:ElementRef;
  private divider: string = "^%|";
  get status() {
    return this._status
  }
  set status(val: any) {
    this._status = val;
    this.comm.removeAllListeners('log-onwrite');
    if (val.isStreaming) {
      this.comm.subscribe('log-onwrite', this.deviceId);
      this.comm.on('log-onwrite', this.onLogStream.bind(this));
    }
  }
  private refreshing: boolean = false;
  private loadingStatus: boolean = false;
  openedLog:any;
  constructor(
    private router: ActivatedRoute,
    private comm: CommService
  ) { }

  ngOnInit() {
    this.router.params.forEach((item) => {
      this.deviceId = item['deviceId'];
      this.refresh();
    })
  }
  refresh() {
    this.refreshing = true;
    Device.fromDeviceUuid(this.deviceId, this.comm)
      .then((d) => {
        this.device = d;
        if (this.device.online) {
          this.getStatus();
        }
      }, this.comm.errorHandler)
      .then(() => this.refreshing = false);
  }

  getStatus() {
    this.loadingStatus = true;
    this.comm.sendMsg("log-status", this.deviceId, null, (err, data) => {
      this.status = data;
      this.loadingStatus = false;
    })
  }
  startStream() {
    this.comm.sendMsg('log-start-stream', this.deviceId, null, (err, data) => {
      this.status = data;
    })
  }
  stopStream() {
    this.comm.sendMsg('log-stop-stream', this.deviceId, null, (err, data) => {
      this.status = data;
    })
  }
  onLogStream(data) {
    this.liveLogs.unshift(this.split(data));
  }
  split(log) {
    const split = log.split(this.divider);
    return {
      ts: split[0],
      level: split[1],
      txt: split[2]
    };
  }
  startRecord() {
    this.comm.sendMsg('log-start-record', this.deviceId, null, (err, data) => {
      this.status = data;
    })
  }
  stopRecord() {
    this.comm.sendMsg('log-stop-record', this.deviceId, null, (err, data) => {
      this.status = data;
    })
  }
  showLogContent(ev){
    var f=ev.file;
    var data=ev.data;
    this.openedLog=ev;
    $(this.logModal.nativeElement).modal();
  }
}



