import { NgModule } from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {ClientLogComponent} from './client-log.component';
import {ClientLogDetailComponent} from './client-log-detail.component';
const routes:Routes=[
  {
    'path':'clientLogs',component:ClientLogComponent
  },
  {
    'path':'clientLog/:deviceId',component:ClientLogDetailComponent
  }
]

@NgModule({
  imports:[
    RouterModule.forChild(routes) 
  ],
  exports:[
  ]
})
export class ClientLogRouteModule{}



