import { Component, OnInit } from '@angular/core';
import {CommService} from '../comm/comm.service';
@Component({
  selector: 'app-client-version',
  templateUrl: './client-version.component.html',
  styleUrls: ['./client-version.component.css']
})
export class ClientVersionComponent implements OnInit {

  webContentUrl:string;
  cfg:any={};
  loading:boolean=false;
  constructor(private comm:CommService) { }

  ngOnInit() {
    this.refresh();
  }

  refresh(){
    this.loading=true;
    this.comm.request("clientVersion/config")
    .then((res)=>{
      this.cfg=res.json();
    },this.comm.errorHandler)
    .then(()=>{this.loading=false});

  }

  setWebContentUrl(){
    this.loading=true;
    var body={
      url:this.cfg.content_url,
      update:this.cfg.update
    }
    this.comm.request('clientVersion/setWebContentUrl',{
      method:"POST",
      body:body
    })
    .then((res)=>{
      this.refresh();
    },this.comm.errorHandler)
    .then(()=>{this.loading=false});
  }
}



