import { Injectable } from '@angular/core';
import { EventEmitter } from 'events';
import { Http, HttpModule, RequestOptionsArgs, Response } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import * as url from 'url';
import * as path from 'path';
const uuid = require('uuid');
declare var io: any;
declare var toastr: any;
@Injectable()
export class CommService extends EventEmitter {


  private host: string;
  private pathName: string;
  private socket: any;
  private responseMap: any = {};
  constructor(private http: Http) {
    super();
    const parsed = url.parse(location.href);
    this.host = `${parsed.protocol}//${parsed.host}`;
    this.pathName = parsed.pathname;
    if (this.pathName[this.pathName.length - 1] === '/') {
      this.pathName = this.pathName.substr(0, this.pathName.length - 2);
    }
    const s = document.createElement("script");
    s.onload = this.initSocketIo.bind(this);
    s.onerror = this.initSocketIoError.bind(this);
    const wsUrl = this.getHost() + "/socket.io/socket.io.js";
    s.src = wsUrl;
    document.querySelector('body').appendChild(s);
  }
  getPath() {
    return this.host + this.pathName;
  }
  getHost() {
    return this.host;
  }
  private initSocketIoError(e) {
    console.error("Cannot init websocket: Cannot load socket.io.js");
  }
  private initSocketIo() {
    this.socket = io(this.getHost());
    this.socket.on('message', this.onMessage.bind(this));
    this.socket.on('response', this.onResponse.bind(this))
    this.on("general-client-type", (data, cb) => {
      cb(null, { type: 'admin' });
    })
    this.on("general-heart-beat", (data, cb) => {
      cb();
    })
    this.emit("socketioready");
  }
  request(url: string, param?: RequestOptionsArgs) {
    const fullUrl = this.getPath()+path.join( "/api" , url);
    return this.http.request(fullUrl, param).toPromise();
  }
  errorHandler(r: Response | Error) {
    const txt = r instanceof Error?r.message:r.text();
    toastr.error(txt);
  }
  showErr(r:Error){
    toastr.error(r.message);
  }

  reply(event: string, err?: any, data?: any) {
    this.socket.emit(event + "-reply", err, data);
  }
  ackMsg(msgId) {
    this.socket.emit('ack', msgId);
  }
  onMessage(params) {
    this.ackMsg(params.msgId);
    this.emit(params.msgType, params.data, (err, data) => {
      this.replyMsg(params.msgId, params.from, params.msgType, err, data);
    })
  }
  replyMsg(msgId, target, msgType, err, data) {
    this.socket.emit('response', {
      target: target,
      msgId: msgId,
      msgType: msgType,
      err: err,
      data: data
    });
  }
  onResponse(params) {
    const msgId = params.msgId;
    if (this.responseMap[msgId]) {
      try {
        this.responseMap[msgId](params.err, params.data);
      } catch (e) {
        console.error(e.stack)
      }
      delete this.responseMap[msgId];
    }
  }
  sendMsg(msgType: string, target: string, data: any, cb) {
    if (!this.socket) {
      this.once('socketioready', () => {
        this.sendMsg(msgType, target, data, cb);
      })
      return;
    }
    var msgId = this.genMsgId();
    this.socket.emit('message', {
      msgType: msgType,
      msgId: msgId,
      target: target,
      data: data
    })
    if (cb) {
      this.responseMap[msgId] = cb;
    }
  }
  genMsgId() {
    return uuid.v4();
  }
  subscribe(msgType: string, target: string) {
    if (!this.socket) {
      this.once('socketioready', () => {
        this.subscribe(msgType, target);
      })
      return;
    }
    this.socket.emit('subscribe', {
      msgType: msgType,
      target: target
    })
  }

}



