import {EventEmitter, Component, OnInit, Input,Output } from '@angular/core';
import {CommService} from '../comm/comm.service';
@Component({
  selector: 'app-client-file',
  templateUrl: './client-file.component.html',
  styleUrls: ['./client-file.component.css']
})
export class ClientFileComponent implements OnInit {
  @Input()
  rootPath:string="/";
  @Input()
  currentPath:string;
  @Input()
  canOpen:Boolean=true;
  @Input()
  canRemove:Boolean=true; 
  @Input()
  deviceId:string;
  @Output()
  onFileLoad:EventEmitter<any>;

  loading:Boolean=false;
  fileList:Array<any>;
  constructor(private comm:CommService) { 
    this.onFileLoad=new EventEmitter();
  }

  ngOnInit() {
    if (!this.currentPath){
      this.currentPath=this.rootPath;
    }
    this.refresh();
  }

  refresh(){
    this.loading=true;
    this.comm.sendMsg("fs-list-folder",this.deviceId,this.currentPath,(err,data)=>{
      this.loading=false;
      if (err){
        this.comm.showErr(err);
      }else{
        this.fileList=data;
      }
    })
  }
  open(f){
    if (f.isDirectory){
      this.currentPath=f.fullPath;
      this.refresh();
    }else{
      this.loading=true;
      this.comm.sendMsg('fs-read',this.deviceId,f.fullPath,(err,data)=>{
        this.loading=false;
        if (err){
          this.comm.showErr(err);
        }else{
          this.onFileLoad.emit({file:f,data:data});
        }
      }) 
    }
  }
  removeFile(f){
    this.loading=true;
    var msg=f.isDirectory?"fs-remove-dir":"fs-remove-file";
    this.comm.sendMsg(msg,this.deviceId,f.fullPath,(err,data)=>{
      if (err){
        this.comm.showErr(err);
      }else{
        this.refresh();
      }
    });
  }


}



