import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import { CommonModule } from '@angular/common';
import {ClientVersionRouteModule} from './client-version.routes.module';
import {ClientVersionComponent} from './client-version.component';
import {CommModule} from '../comm/comm.module';
@NgModule({
  imports: [
    CommonModule,
    ClientVersionRouteModule,
    CommModule,
    FormsModule
  ],
  declarations: [
    ClientVersionComponent
  ],
  exports:[
    ClientVersionComponent
  ]
})
export class ClientVersionModule { }



